.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.wrapper {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-display: swap;
}
body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  user-select: none;
}

fieldset {
  border: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

button {
  cursor: pointer;
}

.main-container {
  width: 100%;
}

.interview {
  display: flex;
  align-items: center;
  padding-left: 136px;
  padding-right: 136px;
  padding-top: 100px;
  padding-bottom: 100px;
  gap: 82px;
  background: radial-gradient(50% 50% at 50% 50%, rgba(184, 125, 88, 0.8) 0%, rgba(184, 125, 88, 0) 100%), radial-gradient(50% 50% at 50% 50%, rgba(58, 95, 86, 0.55) 0%, rgba(58, 95, 86, 0) 100%), rgb(244, 243, 240);
  border-radius: 0px;
  background-repeat: no-repeat;
  background-position: -222px 602px, 976px -100px, 0 0;
}

.interview-text__title {
  font-family: "Source serif pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: rgb(58, 95, 86);
  font-size: 48px;
  line-height: 57px;
  margin: 0;
}

.interview-text__description {
  max-width: 534px;
  font-size: 16px;
  line-height: 22px;
}

.interview-text__button {
  width: 188px;
  height: 44px;
  color: rgb(255, 255, 255);
  line-height: 22px;
}

.interview__photo {
  max-width: 552px;
  width: 100%;
  max-height: 773px;
  background: transparent;
}

.services {
  text-align: center;
  padding-top: 134px;
  padding-right: 136px;
  padding-bottom: 100px;
  padding-left: 136px;
  background: radial-gradient(50% 50% at 50% 50%, rgba(58, 95, 86, 0.376) 0%, rgba(58, 95, 86, 0) 100%);
  background-repeat: no-repeat;
  background-position: -460px 993px;
}

.services__title {
  font-family: "Source serif pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: rgb(58, 95, 86);
  margin: 0;
  position: relative;
}

.services__title::before {
  position: absolute;
  display: block;
  content: "";
  background-image: url("../../img/stripe.svg");
  background-repeat: no-repeat;
  top: -35px;
  left: 50%;
  transform: translateX(-50%);
  width: 160px;
  height: 19px;
}

.services__description {
  max-width: 591px;
  margin: 0 auto;
  margin-top: 11px;
  margin-bottom: 47px;
}

.services__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  padding: 0;
  margin: 0;
  justify-content: center;
}

.service-item {
  background-color: #F4F3F0;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.03);
  border-radius: 16px;
  width: 379px;
  height: 304px;
  text-align: left;
  padding-top: 87px;
  padding-right: 40px;
  padding-left: 30px;
}

.service-item__title {
  font-family: "Source serif pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: rgb(58, 95, 86);
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}

.service-item__depiction {
  margin: 0;
  margin-top: 6px;
}

.service-item__link {
  position: relative;
  color: rgb(58, 95, 86);
  margin-top: 17px;
}

.service-item__link:hover {
  color: #273f39;
  transition-duration: 0.3s;
}

.service-item__link::before {
  position: absolute;
  display: block;
  content: "";
  background-image: url("../../img/arrow.svg");
  background-repeat: no-repeat;
  top: 7px;
  left: 85px;
  width: 19px;
  height: 10px;
}

.preset-store {
  text-align: center;
  padding-top: 134px;
  padding-bottom: 99px;
  padding-left: 136px;
  padding-right: 136px;
  background-color: #F4F3F0;
  background-image: radial-gradient(50% 50% at 50% 50%, rgba(219, 52, 59, 0.296) 0%, rgba(219, 52, 59, 0) 100%);
  background-repeat: no-repeat;
  background-position: 800px 537px;
}

.preset-store__title {
  font-family: "Source serif pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: rgb(58, 95, 86);
  position: relative;
  color: rgb(219, 52, 59);
  margin: 0;
}

.preset-store__title::before {
  position: absolute;
  display: block;
  content: "";
  background-image: url("../../img/stripe.svg");
  background-repeat: no-repeat;
  top: -35px;
  left: 50%;
  transform: translateX(-50%);
  width: 160px;
  height: 19px;
}

.preset-store__description {
  max-width: 591px;
  margin: 0 auto;
  margin-top: 11px;
  margin-bottom: 47px;
}

.preset-store__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  padding: 0;
  margin: 0;
  justify-content: center;
}

.preset-store__button {
  margin-top: 30px;
  position: relative;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  background-color: transparent;
  border: 0;
}

.preset-store__button::before {
  position: absolute;
  display: block;
  content: "";
  background-image: url("../../img/arrow.svg");
  background-repeat: no-repeat;
  top: 12px;
  left: 100px;
  width: 19px;
  height: 10px;
}

.store-item {
  width: 379px;
  height: 438px;
  background-color: rgb(255, 255, 255);
  border-radius: 0px 0px 16px 16px;
  text-align: left;
}

.store-item__title {
  font-family: "Source serif pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: rgb(58, 95, 86);
  font-size: 24px;
  line-height: 33px;
  margin: 0;
  margin-top: 30px;
  margin-left: 30px;
}

.store-item__depiciton {
  font-size: 16px;
  line-height: 22px;
  max-width: 280px;
  margin: 0;
  margin-left: 30px;
}

.contacts {
  background-color: rgb(255, 255, 255);
  padding-top: 101px;
  padding-bottom: 100px;
  background-image: radial-gradient(50% 50% at 50% 50%, rgba(184, 125, 88, 0.35) 0%, rgba(184, 125, 88, 0) 100%);
  background-repeat: no-repeat;
  background-position: -100px 159px;
}

.contact {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 800px;
  height: 259px;
  background-color: rgb(244, 243, 240);
  margin: 0 auto;
  padding-left: 80px;
  padding-right: 80px;
  border-radius: 16px;
}

.contact__depiciton {
  font-size: 28px;
  line-height: 33px;
  width: 313px;
}

.contact__button {
  width: 125px;
  height: 58px;
  color: rgb(255, 255, 255);
  margin-left: auto;
}

.footer-lists {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 52px;
}

.footer-pages {
  width: 100%;
  max-width: 185px;
}

.footer-pages__title {
  text-transform: uppercase;
  color: rgb(184, 125, 88);
}

.footer-pages__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.footer-pages__item {
  margin-bottom: 9px;
}

.footer-pages__item:hover {
  color: #b87d58;
  transition-duration: 0.3s;
}

.footer-pages__item:last-child {
  margin-bottom: 0;
}

.footer-block {
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
  column-gap: 50px;
}

.footer-newspapers__title {
  text-transform: uppercase;
  color: rgb(184, 125, 88);
}

.footer-newspapers__depiction {
  width: 404px;
}

.footer-newspapers__input {
  width: 458px;
  height: 53px;
  color: rgb(229, 231, 235);
  border-radius: 8px;
  border: 0;
  padding-left: 20px;
  font-size: 14px;
  line-height: 22px;
}

.main-header {
  width: 100%;
}

.main-header__wrapper {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 136px;
  padding-right: 136px;
  width: 100%;
  display: flex;
  align-items: center;
}

.main-header__logo {
  height: 30px;
  margin-right: 20px;
}

.main-nav {
  display: flex;
  margin-left: auto;
}

.main-nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
  gap: 20px;
  padding: 0;
  margin: 0;
}

.main-nav__item {
  line-height: 16px;
  height: 26px;
  display: grid;
  place-items: center;
}

.main-nav__item:hover {
  color: rgb(58, 95, 86);
  transition-duration: 0.3s;
}

.main-nav__button {
  width: 122px;
  height: 44px;
  line-height: 22px;
  color: white;
  margin-left: 30px;
  background-image: url("../../img/shop.svg");
  background-repeat: no-repeat;
  background-position: 18px 14px;
  background-size: 17px 17px;
}

.green-button {
  background-color: rgb(58, 95, 86);
  border: 0;
  border-radius: 6px;
}

.green-button:hover {
  background-color: #273f39;
  transition-duration: 0.3s;
}

.main-footer {
  background-color: #292C32;
  color: rgb(255, 255, 255);
  width: 100%;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 136px;
  padding-right: 136px;
}

.footer__logo {
  margin: 0 auto;
}

.footer__copyrights {
  display: block;
  margin-top: 30px;
}